export function preloadList() {
  // запрос списка персонажей
  let url = 'https://downloader.disk.yandex.ru/disk/56e21f3b1bf69aa9e96729b4bc9c82e5c6f1df510d68102830c7c3dbd8f7a605/59b8b49c/5aGtKp3Z-TlKIeltBAP8VthuZhHj_CDKI7Ld5FkoB7U9oJ_UqYo6PPLEUU81_fbTanFELTQmKezcZkFph34yUQ%3D%3D?uid=0&filename=roles.json&disposition=attachment&hash=8Dv/i3tkc7rJUudr/sbnl7/UBdZw5PsGqq8ngL6iUec%3D&limit=0&content_type=application%2Foctet-stream&fsize=137&hid=d4f50876f786fbda36faea074108a07e&media_type=data&tknv=v2';
  return request(url);
}

export function preloadRoles() {
  // запрос списка ролей
  let url = 'https://downloader.disk.yandex.ru/disk/264c6e0aa37f01723fd4a35877f5d8f837f33d4dcdadcf8d89360c880952df62/59b8b499/5aGtKp3Z-TlKIeltBAP8Vq6Mf8pFeg6_2GU7DPmveUV1eRbaUDsGUNX68tXAQDdgtq6CIBRguIYv5Zbf4NNA6w%3D%3D?uid=0&filename=persons.json&disposition=attachment&hash=mnuwGxUf7hM8ScvK3yc3cCLDC8vF%2BFMYeO7/97%2B2J2o%3D&limit=0&content_type=application%2Foctet-stream&fsize=12146&hid=e44c4752b9fa7a7e769e9fb0a4abeace&media_type=data&tknv=v2';
  return request(url);
}
    
function request(url) {
  return new Promise(function(resolve, reject) {
    var xhr = new XMLHttpRequest();
    
    xhr.open('GET', url, false);
    xhr.onload = function() {
      if (this.status === 200) {
        resolve(JSON.parse(this.response));
      } 
      else {
        var error = new Error(this.statusText);
        error.code = this.status;
        reject(error);
      }
    };
    
    xhr.onerror = function() {
      reject(new Error('Network Error'));
    };

    xhr.send();
  });
}


